<template>
  <view class="run">
    <!-- AI Robot Component -->
    <ai-chat-robot 
      v-model:visible="showAiRobot" 
      :run-data="currentRunData"
      @share="handleShareToTeacher"
    />
    
    <!-- AI Robot Float Button -->
    <view class="ai-float-btn" @click="openAiRobot" v-if="isRunning || distance > 0">
      <text class="ai-btn-icon">ü§ñ</text>
      <text class="ai-btn-text">AIÂä©Êâã</text>
    </view>

    <!-- 0. È°∂ÈÉ®Â§©Ê∞î‰∏éÊàêÂ∞±ÔºàÊñ∞Â¢ûÔºâ -->
    <view class="top-widgets">
      <view class="weather-widget">
        <view class="weather-left">
          <text class="weather-temp">24¬∞C</text>
          <text class="weather-status">‚òÄÔ∏è Êô¥Êúó</text>
        </view>
        <view class="weather-right">
          <text class="weather-tips">Á©∫Ê∞î‰ºò ¬∑ ÈÄÇÂÆúË∑ëÊ≠•</text>
        </view>
      </view>
      <scroll-view scroll-x class="achievements-scroll" :show-scrollbar="false">
        <view class="badge-item" v-for="(badge, idx) in achievements" :key="idx">
          <text class="badge-icon">{{ badge.icon }}</text>
          <text class="badge-name">{{ badge.name }}</text>
        </view>
      </scroll-view>
    </view>

    <!-- 1. ÊêúÁ¥¢ÊâìÂç°ÁÇπÔºà‰ªÖÊ†°Âõ≠ÊâìÂç°Áî®Ôºâ -->
    <view class="search-bar" v-if="currentMode === 'campus'">
      <input 
        v-model="checkpointName" 
        placeholder="ËæìÂÖ•Ê†°Âõ≠ÊâìÂç°ÁÇπÔºàÂ¶ÇÔºöÊìçÂú∫/Ë∑ëÈÅìÔºâ"
        class="search-input"
      />
      <button @click="searchCheckpoint" class="search-btn">ÊêúÁ¥¢</button>
    </view>

    <!-- 2. Âú∞ÂõæÂ±ïÁ§∫ -->
    <view class="overview-card">
      <text class="overview-title">‰ªäÊó•Ë∑ëÊ≠•Ê¶ÇËßà</text>
      <view class="overview-meta">
        <text>Ê¨°Êï∞Ôºö{{ todayRunCount }}</text>
        <text>ÈáåÁ®ãÔºö{{ todayRunDistance }} km</text>
      </view>
      <text v-if="teacherRunTask" class="task-tip">ÊïôÂ∏à‰ªªÂä°Ôºö{{ teacherRunTask }}</text>
    </view>
    <map 
      class="map" 
      :latitude="lat" 
      :longitude="lng" 
      :markers="markers"
      :polyline="polyline"
    ></map>

    <!-- 2.5 Êé®ËçêË∑ØÁ∫øÔºàÊñ∞Â¢ûÔºâ -->
    <view class="routes-card" v-if="currentMode === 'normal'">
      <view class="card-header" @click="toggleRoutes">
        <text class="card-title">üèÉ Êé®ËçêË∑ØÁ∫ø</text>
        <text class="card-toggle">{{ showRoutes ? 'Êî∂Ëµ∑' : 'Â±ïÂºÄ' }}</text>
      </view>
      <view class="routes-list" v-if="showRoutes">
        <view class="route-item" v-for="(route, idx) in recommendRoutes" :key="idx" @click="useRoute(route)">
          <view class="route-info">
            <text class="route-name">{{ route.name }}</text>
            <text class="route-meta">{{ route.distance }}km ¬∑ {{ route.difficulty }}</text>
          </view>
          <text class="route-action">ÂéªË∑ëÊ≠• ></text>
        </view>
      </view>
    </view>

    <!-- 3. Ê†∏ÂøÉË∑ëÊ≠•Ê®°ÂºèÂàáÊç¢ÔºàÊôÆÈÄö/Ë≠¶Âä°/Ê†°Âõ≠Ôºå‰∏âÈÄâ‰∏ÄÔºâ -->
    <view class="mode-switch">
      <text class="mode-item" :class="{active: currentMode === 'normal'}" @click="switchMode('normal')">ÊôÆÈÄöË∑ëÊ≠•</text>
      <text class="mode-item" :class="{active: currentMode === 'police'}" @click="switchMode('police')">Ë≠¶Âä°‰∏ìÈ°π</text>
      <text class="mode-item" :class="{active: currentMode === 'campus'}" @click="switchMode('campus')">Ê†°Âõ≠ÊâìÂç°</text>
    </view>

    <!-- 4. Ë≠¶Âä°‰∏ìÈ°πËÆ°ÂàíÔºàÁã¨Á´ãÊ®°ÂùóÔºå‰ªÖË≠¶Âä°Ê®°ÂºèÊòæÁ§∫Ôºâ -->
    <view v-if="currentMode === 'police'" class="police-plan">
      <text class="plan-title">üö® ÂÖ¨ÂÆâÈô¢Ê†°2000Á±≥Ë∑ë‰∏ìÈ°πËÆ≠ÁªÉ</text>
      <view class="plan-info">
        <text class="info-item">ÁõÆÊ†áË∑ùÁ¶ªÔºö<span class="highlight">{{policeTargetDistance/1000}}ÂÖ¨Èáå</span></text>
        <text class="info-item">ËææÊ†áÈÖçÈÄüÔºö<span class="highlight">{{policeTargetPace}}ÂàÜÈíü/ÂÖ¨Èáå</span></text>
        <text class="info-item">ËÄÉÊ†∏Ê†áÂáÜÔºö<span class="highlight">Áî∑Áîü‚â§6ÂàÜ30Áßí/ÂÖ¨Èáå | Â•≥Áîü‚â§7ÂàÜ30Áßí/ÂÖ¨Èáå</span></text>
      </view>
    </view>

    <!-- 5. ÊôÆÈÄöË∑ëÊ≠• -->
    <view v-if="currentMode === 'normal'" class="run-mode-box">
      <view v-if="!isRunning" class="start-box">
        <text class="tip">Êó†Âú∞ÁÇπ/Ë∑ùÁ¶ªÈôêÂà∂ÔºåËá™Áî±ËÆ∞ÂΩïË∑ëÊ≠•ËΩ®Ëøπ</text>
        <button @click="startNormalRun" class="start-btn">ÂºÄÂßãË∑ëÊ≠•</button>
      </view>
      <view v-else class="running-box">
        <text class="data">Êó∂ÈïøÔºö{{duration}}Áßí | Â∑≤Ë∑ëÔºö{{(distance/1000).toFixed(2)}}km | Ê≠•Êï∞Ôºö{{stepCount}} | ÂøÉÁéáÔºö{{heartRate}}Ê¨°/ÂàÜ</text>
        <view class="progress-wrap">
          <view class="progress-bar"><view class="progress-fill" :style="{width: normalProgress + '%'}"></view></view>
          <text class="progress-text">‰ªäÊó•ÁõÆÊ†á {{dailyTarget}} km ¬∑ ÂÆåÊàê {{(distance/1000).toFixed(2)}} km</text>
        </view>
        <button @click="stopRun" class="stop-btn">ÁªìÊùüË∑ëÊ≠•</button>
      </view>
    </view>

    <!-- 6. Ë≠¶Âä°‰∏ìÈ°πË∑ëÊ≠•ÔºàÊåâ2000Á±≥ÁõÆÊ†áË∑ëÔºâ -->
    <view v-if="currentMode === 'police'" class="run-mode-box">
      <view v-if="!isRunning" class="start-box">
        <text class="tip">ÊåâÂÖ¨ÂÆâËÄÉÊ†∏Ê†áÂáÜÂÆåÊàê2000Á±≥Ë∑ëÔºåËá™Âä®Ê†°È™åÈÖçÈÄüÊòØÂê¶ËææÊ†á</text>
        <button @click="startPoliceRun" class="start-btn">ÂºÄÂßã‰∏ìÈ°πËÆ≠ÁªÉ</button>
      </view>
      <view v-else class="running-box">
        <text class="data">Êó∂ÈïøÔºö{{duration}}Áßí | Â∑≤Ë∑ëÔºö{{(distance/1000).toFixed(2)}}km / ÁõÆÊ†áÔºö2km</text>
        <text class="data">Ââ©‰ΩôÔºö{{((policeTargetDistance - distance)/1000).toFixed(2)}}km | ÈÖçÈÄüÔºö{{currentPace.toFixed(1)}}ÂàÜÈíü/ÂÖ¨Èáå</text>
        <text class="data">ÂøÉÁéáÔºö{{heartRate}}Ê¨°/ÂàÜ | Ê≠•Êï∞Ôºö{{stepCount}}</text>
        <text class="pace-status" :style="{color: currentPace <= policeTargetPace ? 'green' : 'red'}">
          {{currentPace <= policeTargetPace ? '‚úÖ ÈÖçÈÄüËææÊ†á' : '‚ùå ÈÖçÈÄüÊú™ËææÊ†á'}}
        </text>
        <!-- ËææÂà∞ÁõÆÊ†áË∑ùÁ¶ªËá™Âä®ÊèêÁ§∫ -->
        <text class="finish-tip" v-if="distance >= policeTargetDistance">üéâ Â∑≤ÂÆåÊàê2000Á±≥ÁõÆÊ†áÔºÅ</text>
        <view class="progress-wrap">
          <view class="progress-bar"><view class="progress-fill" :style="{width: policeProgress + '%'}"></view></view>
          <text class="progress-text">‰∏ìÈ°πÁõÆÊ†á 2 km ¬∑ ÂÆåÊàê {{(distance/1000).toFixed(2)}} km</text>
        </view>
        <button @click="stopRun" class="stop-btn">ÁªìÊùüËÆ≠ÁªÉ</button>
      </view>
    </view>

    <!-- 7. Ê†°Âõ≠ÊâìÂç° -->
    <view v-if="currentMode === 'campus'" class="run-mode-box">
      <view v-if="!checkpoint.name" class="no-checkpoint">
        <text class="tip">ËØ∑ÂÖàÊêúÁ¥¢Ê†°Âõ≠ÊâìÂç°ÁÇπ</text>
      </view>
      <view v-else>
        <view v-if="!isRunning" class="start-box">
          <text class="checkpoint-info">ÊâìÂç°ÁÇπÔºö{{checkpoint.name}}ÔºàÈúÄÂà∞Ëææ10Á±≥ÂÜÖÔºâ</text>
          <button @click="startCampusRun" class="start-btn">ÂºÄÂßãÊâìÂç°</button>
        </view>
        <view v-else class="running-box">
          <text class="data">Êó∂ÈïøÔºö{{duration}}Áßí | Ë∑ùÊâìÂç°ÁÇπÔºö{{distanceToCheckpoint}}Á±≥ | Ê≠•Êï∞Ôºö{{stepCount}} | ÂøÉÁéáÔºö{{heartRate}}Ê¨°/ÂàÜ</text>
          <text class="reach-status" :style="{color: isReach ? 'green' : 'red'}">
            {{isReach ? '‚úÖ Â∑≤Âà∞ËææÊâìÂç°ÁÇπ' : '‚ùå Êú™Âà∞ËææÊâìÂç°ÁÇπ'}}
          </text>
          <button @click="stopRun" class="stop-btn">ÁªìÊùüÊâìÂç°</button>
        </view>
      </view>
    </view>
    
    <!-- Â∫ïÈÉ®ÂØºËà™Ê†è -->
    <CustomTabBar current="/pages/run/run" />
  </view>
</template>

<script setup>
// Áªü‰∏ÄÂØºÂÖ•ËßÑËåÉ
import { ref, computed, onUnmounted } from 'vue';
import { onShow, onLoad } from '@dcloudio/uni-app';
import AiChatRobot from '@/components/ai-chat-robot/ai-chat-robot.vue';
import CustomTabBar from '@/components/CustomTabBar/CustomTabBar.vue';

// ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜÂÆöÊó∂Âô®
onUnmounted(() => {
  if (timer) clearInterval(timer);
  stopStepCount();
});

// AI Robot Logic
const showAiRobot = ref(false);
const currentRunData = computed(() => ({
  distance: distance.value,
  pace: currentPace.value || (distance.value > 0 ? (duration.value/60)/(distance.value/1000) : 0),
  heartRate: heartRate.value,
  stepCount: stepCount.value
}));

const openAiRobot = () => {
  showAiRobot.value = true;
};

const handleShareToTeacher = (card) => {
  // Save shared report to storage for teacher to see (mock)
  const report = {
    studentName: uni.getStorageSync('userInfo')?.name || 'Â≠¶Âëò',
    time: new Date().toLocaleString(),
    card: card
  };
  // In a real app, this would be an API call. 
  // Here we mock it by saving to a global list that teacher page reads.
  let sharedReports = uni.getStorageSync('mockSharedReports') || [];
  sharedReports.unshift(report);
  uni.setStorageSync('mockSharedReports', sharedReports);
};

// Ê¶ÇËßà‰∏é‰ªªÂä°ÊèêÁ§∫
const todayRunCount = ref(0);
const todayRunDistance = ref(0);
const teacherRunTask = ref('');
const dailyTarget = ref(5);
const normalProgress = ref(0);
const policeProgress = ref(0);
const historyList = ref([]);

// Êñ∞Â¢ûÊï∞ÊçÆ
const achievements = ref([
  { name: 'ÂàùÊ¨°ÂºÄË∑ë', icon: 'üèÖ' },
  { name: '‰∫îÂÖ¨ÈáåËææ‰∫∫', icon: 'üèÉ‚Äç‚ôÇÔ∏è' },
  { name: 'ÂÖ®Âã§Âë®', icon: 'üî•' },
  { name: 'Êó©Ëµ∑È∏ü', icon: 'üê¶' }
]);
const showRoutes = ref(false);
const recommendRoutes = ref([
  { name: 'ÁéØÊ†°Â§ñÂúàË∑ë', distance: 5.2, difficulty: '‰∏≠Á≠â' },
  { name: 'ÊπñÁïîÊûóËç´ÈÅì', distance: 3.0, difficulty: 'ÁÆÄÂçï' },
  { name: '‰ΩìËÇ≤Âú∫ÂÜ≤Âà∫', distance: 1.5, difficulty: 'Âõ∞Èöæ' }
]);
const toggleRoutes = () => showRoutes.value = !showRoutes.value;
const useRoute = (route) => {
  uni.showToast({ title: `Â∑≤Âä†ËΩΩË∑ØÁ∫øÔºö${route.name}`, icon: 'none' });
  dailyTarget.value = route.distance;
};

// 1. Âú∞Âõæ/ÊâìÂç°ÁÇπÊï∞ÊçÆ
const checkpointName = ref('');
const lat = ref(0);
const lng = ref(0);
const markers = ref([]);
const polyline = ref([]);
const checkpoint = ref({});

// 2. Ë∑ëÊ≠•Ê†∏ÂøÉÈÖçÁΩÆ
const currentMode = ref('normal'); // normal-ÊôÆÈÄö police-Ë≠¶Âä° campus-Ê†°Âõ≠
const isRunning = ref(false);
const duration = ref(0);
const distance = ref(0); // Â∑≤Ë∑ëË∑ùÁ¶ªÔºàÁ±≥Ôºâ
const distanceToCheckpoint = ref(0);
const isReach = ref(false);
const stepCount = ref(0);
const heartRate = ref(80);
let timer = null;
let accelerometerListener = null;

// 3. Ë≠¶Âä°‰∏ìÈ°πÂõ∫ÂÆöÈÖçÁΩÆÔºàÊåâÂÖ¨ÂÆâËÄÉÊ†∏Ê†áÂáÜÔºâ
const policeTargetDistance = ref(2000); // Âõ∫ÂÆö2000Á±≥
const policeTargetPace = ref(6.5); // ËææÊ†áÈÖçÈÄüÔºö6.5ÂàÜÈíü/ÂÖ¨ÈáåÔºàÁî∑ÁîüÊ†áÂáÜÔºâ
// ËÆ°ÁÆóÂΩìÂâçÈÖçÈÄüÔºàÂàÜÈíü/ÂÖ¨ÈáåÔºâ
const currentPace = computed(() => {
  const km = distance.value / 1000;
  const min = duration.value / 60;
  return km === 0 ? 0 : min / km;
});

// Êé•Êî∂È°µÈù¢ÂèÇÊï∞
onLoad((options) => {
  if (options.mode) {
    currentMode.value = options.mode;
  }
  if (options.target) {
    policeTargetDistance.value = parseInt(options.target);
  }
  if (options.course) {
    uni.showToast({ title: `ÂºÄÂßãËØæÁ®ãÔºö${options.course}`, icon: 'none' });
  }
});

// È°µÈù¢ÊòæÁ§∫Êó∂ÂàùÂßãÂåñ
onShow(() => {
    const role = uni.getStorageSync('userRole') || uni.getStorageSync('role');
    if (role === 'teacher') {
      uni.showToast({ title: 'ËØ•ÂäüËÉΩ‰ªÖÂØπÂ≠¶ÁîüÂºÄÊîæ', icon: 'none' });
      setTimeout(() => {
        uni.redirectTo({ url: '/pages/teacher/home/home' });
      }, 800);
      return;
    }
  // 1. Â§ÑÁêÜ‰ªéÈ¶ñÈ°µË∑≥ËΩ¨ËøáÊù•ÁöÑÊ®°ÂºèÂèÇÊï∞ (Âõ† switchTab ‰∏çÊîØÊåÅ URL ‰º†ÂèÇ)
  const targetMode = uni.getStorageSync('runMode');
  if (targetMode) {
    switchMode(targetMode); // ‰ΩøÁî® switchMode ÊñπÊ≥ï‰ª•Á°Æ‰øùÁä∂ÊÄÅÈáçÁΩÆ
    uni.removeStorageSync('runMode'); // Ê∂àË¥πÂêéÊ∏ÖÈô§
  }

  getLocation();
  checkpoint.value = uni.getStorageSync('checkpoint') || {};
  if (checkpoint.value.name) {
    addCheckpointMarker(checkpoint.value.lat, checkpoint.value.lng, checkpoint.value.name);
  }
  const records = uni.getStorageSync('runRecordsList') || [];
  const now = new Date();
  const dayStart = new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime();
  const dayEnd = dayStart + 24 * 60 * 60 * 1000;
  let c = 0;
  let d = 0;
  records.forEach(r => {
    const t = new Date(r.createTime).getTime();
    const isRunType = r.type ? r.type === 'run' : true;
    if (isRunType && t >= dayStart && t < dayEnd) {
      c += 1;
      d += Number(r.distance) || 0;
    }
  });
  todayRunCount.value = c;
  todayRunDistance.value = Number(d.toFixed(2));
  historyList.value = buildHistory(records);
  const taskStr = uni.getStorageSync('teacherTask');
  if (taskStr) {
    try {
      const obj = typeof taskStr === 'string' ? JSON.parse(taskStr) : taskStr;
      teacherRunTask.value = obj.title || '';
    } catch (e) {
      teacherRunTask.value = '';
    }
  }
});

// 4. ÂÆö‰Ωç‰ºòÂåñÔºàÂê´ÊùÉÈôêÁî≥ËØ∑+Ê†°Âõ≠Âõ¥Ê†èÔºâ
const getLocation = () => {
  // #ifdef H5
  doGetLocation();
  // #endif

  // #ifndef H5
  uni.authorize({
    scope: 'scope.userLocation',
    success: () => {
      doGetLocation();
    },
    fail: () => {
      uni.showModal({
        title: 'ÊùÉÈôêÁî≥ËØ∑',
        content: 'ÈúÄË¶ÅÂÆö‰ΩçÊùÉÈôêÊâçËÉΩ‰ΩøÁî®ÊâìÂç°/Ë∑ëÊ≠•ÂäüËÉΩÔºåËØ∑ÂâçÂæÄËÆæÁΩÆÂºÄÂêØ',
        confirmText: 'ÂéªËÆæÁΩÆ',
        success: (res) => {
          if (res.confirm) uni.openSetting();
        }
      });
    }
  });
  // #endif
};

const doGetLocation = () => {
  uni.getLocation({
    type: 'gcj02',
    accuracy: 'high',
    success: (res) => {
      lat.value = res.latitude;
      lng.value = res.longitude;
      markers.value = [{
        id: 0,
        latitude: res.latitude,
        longitude: res.longitude,
        title: 'ÊàëÁöÑ‰ΩçÁΩÆ',
        iconPath: '/static/location.png',
        width: 30,
        height: 30
      }];
      // Ê†°Âõ≠Âõ¥Ê†èÔºà‰ªÖÊ†°Âõ≠ÊâìÂç°Áî®Ôºâ
      const campusLatMin = 39.90;
      const campusLatMax = 39.92;
      const campusLngMin = 116.39;
      const campusLngMax = 116.41;
      const isInCampus = res.latitude >= campusLatMin && res.latitude <= campusLatMax 
                      && res.longitude >= campusLngMin && res.longitude <= campusLngMax;
      if (!isInCampus && currentMode.value === 'campus') {
        uni.showToast({ title: '‰ªÖÊ†°Âõ≠ÂÜÖÂèØËøõË°åÊâìÂç°', icon: 'none' });
      }
    },
    fail: (err) => {
      console.error('Location failed:', err);
      let msg = 'ÂÆö‰ΩçÂ§±Ë¥•ÔºåÂ∑≤‰ΩøÁî®Ê®°Êãü‰ΩçÁΩÆ';
      
      // #ifdef H5
      // ChromeÁ≠âÊµèËßàÂô®ÈôêÂà∂ÈùûHTTPSÁéØÂ¢ÉÊó†Ê≥ï‰ΩøÁî®ÂÆö‰ΩçÔºàlocalhostÈô§Â§ñÔºâ
      if (location.protocol !== 'https:' && location.hostname !== 'localhost' && location.hostname !== '127.0.0.1') {
        msg = 'H5ÂÆö‰ΩçÈúÄHTTPSÔºåÂ∑≤ÂàáÊ®°Êãü‰ΩçÁΩÆ';
      } else if (err.errMsg && err.errMsg.indexOf('auth') !== -1) {
        msg = 'ÂÆö‰ΩçÊùÉÈôêË¢´ÊãíÔºåÂ∑≤ÂàáÊ®°Êãü‰ΩçÁΩÆ';
      }
      // #endif

      uni.showToast({ title: msg, icon: 'none', duration: 3000 });
      
      // ÈôçÁ∫ß‰ΩøÁî®Ê®°Êãü‰ΩçÁΩÆÔºàÂåó‰∫¨Â§©ÂÆâÈó®ÈôÑËøëÔºâ
      lat.value = 39.908823;
      lng.value = 116.397470;
      markers.value = [{ 
        id: 0, 
        latitude: 39.908823, 
        longitude: 116.397470, 
        title: 'ÊàëÁöÑ‰ΩçÁΩÆ (Ê®°Êãü)', 
        iconPath: '/static/location.png', 
        width: 30, 
        height: 30 
      }];
    }
  });
};

// 5. ÊêúÁ¥¢ÊâìÂç°ÁÇπÔºà‰ªÖÊ†°Âõ≠Ê®°ÂºèÔºâ
const searchCheckpoint = () => {
  if (!checkpointName.value) {
    uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊâìÂç°ÁÇπÂêçÁß∞', icon: 'none' });
    return;
  }
  const newCheckpoint = {
    name: checkpointName.value,
    lat: lat.value + 0.001,
    lng: lng.value + 0.001
  };
  uni.setStorageSync('checkpoint', newCheckpoint);
  checkpoint.value = newCheckpoint;
  addCheckpointMarker(newCheckpoint.lat, newCheckpoint.lng, newCheckpoint.name);
  polyline.value = [{
    points: [
      { latitude: lat.value, longitude: lng.value },
      { latitude: newCheckpoint.lat, longitude: newCheckpoint.lng }
    ],
    color: '#FF0000',
    width: 5
  }];
  uni.showToast({ title: `ÊâæÂà∞${newCheckpoint.name}`, icon: 'success' });
};

// 6. Ê∑ªÂä†ÊâìÂç°ÁÇπÊ†áËÆ∞
const addCheckpointMarker = (lat, lng, name) => {
  markers.value.push({
    id: 1,
    latitude: lat,
    longitude: lng,
    title: name,
    iconPath: '/static/checkpoint.png',
    width: 40,
    height: 40
  });
};

// 7. ÂàáÊç¢Ë∑ëÊ≠•Ê®°ÂºèÔºàÊôÆÈÄö/Ë≠¶Âä°/Ê†°Âõ≠Ôºâ
const switchMode = (mode) => {
  // ÂàáÊç¢Ê®°ÂºèÊó∂ÈáçÁΩÆÊâÄÊúâË∑ëÊ≠•Áä∂ÊÄÅ
  isRunning.value = false;
  clearInterval(timer);
  stopStepCount();
  duration.value = 0;
  distance.value = 0;
  stepCount.value = 0;
  heartRate.value = 80;
  currentMode.value = mode;
};

// 8. Ê≠•Êï∞ÁªüËÆ°ÔºàÂä†ÈÄüÂ∫¶‰º†ÊÑüÂô®Ôºâ
const startStepCount = () => {
  accelerometerListener = uni.onAccelerometerChange((res) => {
    const acceleration = Math.sqrt(res.x*res.x + res.y*res.y + res.z*res.z);
    if (acceleration > 15) stepCount.value += 1;
  });
};
const stopStepCount = () => {
  if (accelerometerListener) {
    uni.offAccelerometerChange(accelerometerListener);
    accelerometerListener = null;
  }
};

// 9. ÂøÉÁéáÊõ¥Êñ∞+È¢ÑË≠¶
const updateHeartRate = () => {
  heartRate.value = 80 + Math.floor(duration.value / 10);
  if (heartRate.value > 180) {
    uni.showModal({
      title: 'ÂÅ•Â∫∑È¢ÑË≠¶',
      content: `ÂΩìÂâçÂøÉÁéáËøáÈ´òÔºà${heartRate.value}Ê¨°/ÂàÜÔºâÔºåÂª∫ËÆÆÈôçÈÄü‰ºëÊÅØ`,
      showCancel: false
    });
  }
};

// 10. ÂºÄÂßãË∑ëÊ≠•ÔºàÂàÜ‰∏âÁßçÊ®°ÂºèÔºâ
// ÊôÆÈÄöË∑ëÊ≠•ÔºàÊó†Âõ∫ÂÆöÁõÆÊ†áÔºâ
const startNormalRun = () => {
  isRunning.value = true;
  duration.value = 0;
  distance.value = 0;
  stepCount.value = 0;
  heartRate.value = 80;
  startStepCount();
  timer = setInterval(() => {
    duration.value += 1;
    distance.value += Math.random() * 5; // Ê®°ÊãüÊØèÁßíË∑ë5Á±≥Â∑¶Âè≥
    normalProgress.value = Math.min(100, ((distance.value/1000) / dailyTarget.value) * 100);
    updateHeartRate();
  }, 1000);
};

// Ë≠¶Âä°‰∏ìÈ°πÔºàÂõ∫ÂÆö2000Á±≥ÔºåÊåâËææÊ†áÈÖçÈÄüË∑ëÔºâ
const startPoliceRun = () => {
  isRunning.value = true;
  duration.value = 0;
  distance.value = 0;
  stepCount.value = 0;
  heartRate.value = 80;
  startStepCount();
  // ÊåâËææÊ†áÈÖçÈÄü6.5ÂàÜÈíü/ÂÖ¨ÈáåÊé®ËøõÔºàÁ∫¶2.56Á±≥/ÁßíÔºâ
  timer = setInterval(() => {
    duration.value += 1;
    distance.value += 2.56; // Á≤æÂáÜÂåπÈÖç6.5ÂàÜÈíü/ÂÖ¨ÈáåÁöÑÈÖçÈÄü
    policeProgress.value = Math.min(100, (distance.value / policeTargetDistance.value) * 100);
    updateHeartRate();
    // ËææÂà∞ÁõÆÊ†áË∑ùÁ¶ªÂºπÁ™óÊèêÁ§∫
    if (distance.value >= policeTargetDistance.value && !uni.getStorageSync('policeFinishTip')) {
      uni.showToast({ title: 'Â∑≤ÂÆåÊàê2000Á±≥ÁõÆÊ†áÔºÅ', icon: 'success' });
      uni.setStorageSync('policeFinishTip', '1');
    }
  }, 1000);
};

// Ê†°Âõ≠ÊâìÂç°
const startCampusRun = () => {
  isRunning.value = true;
  duration.value = 0;
  distanceToCheckpoint.value = 50;
  isReach.value = false;
  stepCount.value = 0;
  heartRate.value = 80;
  startStepCount();
  timer = setInterval(() => {
    duration.value += 1;
    distanceToCheckpoint.value = Math.max(0, distanceToCheckpoint.value - 0.5);
    isReach.value = distanceToCheckpoint.value <= 10;
    updateHeartRate();
  }, 1000);
};

// 11. ÁªìÊùüË∑ëÊ≠•ÔºàÁªü‰∏ÄÈÄªËæëÔºâ
const stopRun = () => {
  clearInterval(timer);
  stopStepCount();
  isRunning.value = false;
  uni.removeStorageSync('policeFinishTip'); // ÈáçÁΩÆÁõÆÊ†áÂÆåÊàêÊèêÁ§∫

  // ÈÖçÈÄüÊ†°È™åÔºà‰ªÖË≠¶Âä°Ê®°ÂºèÔºâ
  if (currentMode.value === 'police') {
    if (distance.value < policeTargetDistance.value) {
      uni.showModal({
        title: 'ËÆ≠ÁªÉÊú™ÂÆåÊàê',
        content: `‰ªÖÂÆåÊàê${(distance.value/1000).toFixed(2)}ÂÖ¨ÈáåÔºåÊú™ËææÂà∞2000Á±≥ÁõÆÊ†á`,
        showCancel: false
      });
    } else if (currentPace.value > policeTargetPace.value) {
      uni.showModal({
        title: 'ÈÖçÈÄüÊú™ËææÊ†á',
        content: `ÂΩìÂâçÈÖçÈÄü${currentPace.value.toFixed(1)}ÂàÜÈíü/ÂÖ¨ÈáåÔºåÊú™ËææÂà∞${policeTargetPace.value}ÂàÜÈíü/ÂÖ¨ÈáåÁöÑËÄÉÊ†∏Ê†áÂáÜ`,
        showCancel: false
      });
    }
  } else {
    // ÊôÆÈÄö/Ê†°Âõ≠Ê®°ÂºèÈÖçÈÄüÊ†°È™å
    const km = distance.value / 1000;
    const min = duration.value / 60;
    const pace = km === 0 ? 0 : min / km;
    if (pace < 3 || pace > 10) {
      uni.showModal({
        title: 'Êï∞ÊçÆÂºÇÂ∏∏',
        content: 'ÈÖçÈÄü‰∏çÂú®ÂêàÁêÜËåÉÂõ¥Ôºà3-10ÂàÜÈíü/ÂÖ¨ÈáåÔºâÔºåÊï∞ÊçÆÂèØËÉΩÊó†Êïà',
        showCancel: false
      });
    }
  }

  // Ë∑≥ËΩ¨ÁªìÁÆóÈ°µÔºàÂ∏¶Ë≠¶Âä°Ê®°ÂºèÂèÇÊï∞Ôºâ
  uni.navigateTo({
    url: `/pages/result/result?mode=${currentMode.value}&duration=${duration.value}&distance=${
      currentMode.value === 'campus' ? 50 - distanceToCheckpoint.value : distance.value
    }&isReach=${isReach.value}&isPoliceFinish=${distance.value >= policeTargetDistance.value}&policePace=${currentPace.value}`
  });
};

const buildHistory = (records) => {
  const days = 7;
  const now = new Date();
  const arr = [];
  for (let i=0;i<days;i++) {
    const day = new Date(now.getFullYear(), now.getMonth(), now.getDate()-i);
    const start = new Date(day.getFullYear(), day.getMonth(), day.getDate()).getTime();
    const end = start + 24*60*60*1000;
    const runRecs = records.filter(r => {
      const t = new Date(r.createTime).getTime();
      const isRunType = r.type ? r.type === 'run' : true;
      return isRunType && t>=start && t<end;
    });
    const count = runRecs.length;
    const distanceSum = runRecs.reduce((s,x)=>s+Number(x.distance||0),0);
    arr.push({ date: `${day.getMonth()+1}/${day.getDate()}`, count, distance: Number(distanceSum.toFixed(2)) });
  }
  return arr.reverse();
};
</script>

